<template>
  <section class="detail">
    <el-form ref="form" label-width="120px">
      <el-form-item label="제목">
        <el-input readonly v-model="form.subj"></el-input>
      </el-form-item>
      <el-form-item label="구분" label-width="">
        <el-input readonly v-model="form.tp"></el-input>
      </el-form-item>
      <el-form-item label="작성일">
        <el-input readonly v-model="form.createdAt"></el-input>
      </el-form-item>
      <el-form-item label="내용">
        <el-input readonly v-html="form.conts"></el-input>
      </el-form-item>
    </el-form>
    <div class="bottomBtns">
      <el-button type="primary">목록</el-button>
      <el-button type="primary" @click="$router.push({path: '/NoticeRegister', query: {noticeId}})">수정</el-button>
      <el-button type="primary">삭제</el-button>
    </div>
  </section>
</template>
<script>
  import { VueEditor } from 'vue2-editor'
  import axios from 'axios'

  export default {
    components: {
      VueEditor
    },
    data() {
      return {
        noticeId: this.$route.query.noticeId,
        form: {
          subj: '',
          tp: '',
          createdAt: '',
          conts: ''
        }
      }
    },
    created() { // 훅
      console.log('this.$route.query = ', this.$route.query)
      //console.log('this.$route.params = ', this.$route.params)
      axios.get(`http://13.125.99.42:3000/notice/${this.noticeId}`)
      .then(res => {
        console.log(res)

        this.form = res.data.form
      })
      .catch(arr => {
        console.log(arr)
      })
      .finally(_ =>{
          
      })
    },
  }
</script>
<style scoped>
  .el-select .el-input {
    width: 110px;
  }

  .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
</style>



