import Vue from 'vue'
import VueRouter from 'vue-router'
//import HelloWorld from '@/views/HelloWorld'
import NoticeList from '@/views/notice/NoticeList.vue'
import NoticeDetail from '@/views/notice/NoticeDetail.vue'
import NoticeRegister from '@/views/notice/NoticeRegister.vue'
import NotFound from '@/404.vue'
import Layout from '@/components/Layout'
import Cookies from 'js-cookie'

Vue.use(VueRouter)

const routes = [
  /*
  {
    path: '/',
    name: 'HelloWorld',
    component: HelloWorld
  },*/
  {path: '/login', component: () => import('@/views/login')},
  {
    path: '/',
    component: Layout,
    redirect: '/helloworld',
    name: 'HelloWorld',
    children: [{
      path: '/helloworld',
      conponent: () => import('@/views/helloworld')
    }]
  },
  {
    path: '/notice',
    component: Layout,
    children: [
      {
        path: 'List',
        name: 'NoticeList',
        component: () => import('@/views/notice/NoticeList.vue'),
        beforeEnter: (to, from, next) => {
          console.log(to)
          console.log(from)
          
          if (Cookies.get('token')){
            if(to.path === '/login') {
              next({ path: `/`})
            } else{
              next()
            }
          } else {
            if (to.path !== '/login') {
              next({path:'/login'})
            }else{
              next()
            }
          }
        }
      },
      {
        path: 'register',
        name: 'NoticeRegister',
        component: () => import('@/views/notice/NoticeRegister.vue')
      },
      {
        path: 'detail',
        name: 'NoticeDetail',
        component: () => import('@/views/notice/NoticeDetail.vue')
      }
    ]
  },
  {
    path: '/popup',
    component: Layout,
    children: [
      {
        path:'register',
        name: 'PopupRegister',
        component: () => import('@/views/popup/PopupRegister')
      }
    ]
  },
  {
    path: '/',
    component: Layout,
    children: [
      {
        path:'registraion',
        name: 'Rregistraion',
        component: () => import('@/views/registraion/Registration')
      }
    ]
  },
  /*
  {
    path: '/NoticeList',
    name: 'NoticeList',
    component: NoticeList
  },
  {
    path: '/NoticeDetail',
    name: 'NoticeDetail',
    component: NoticeDetail
  },
  {
    path: '/NoticeRegister',
    name: 'NoticeRegister',
    component: NoticeRegister
  },*/
  {
    path: '*',
    component: NotFound
  }
]

export default new VueRouter({
  routes
})



