<template>
  <section class="detail">
    {{form}}
    <el-form ref="form" label-width="150px">
      <el-form-item label="제목">
        <el-input v-model="form.subj"></el-input>
      </el-form-item>
      <el-form-item label="헤드라인 등록">
        <el-radio-group v-model="form.dpTp">
          <el-radio label="헤드라인"></el-radio>
          <el-radio label="일반"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="팝업개시 대상" prop="">
        <el-checkbox-group v-model="form.init">
          <el-checkbox label="PC Web"></el-checkbox>
          <el-checkbox label="Mobile Web"></el-checkbox>
          <el-checkbox label="APP"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="내용">
        <vue-editor id="editor" v-model="form.conts"></vue-editor>
      </el-form-item>

      <ul>
        <li v-for="(item, index) in bodyData" :key="index"> <!-- item하고, index는 사용자 지정, index를 i로 바꾸면 key값도 i로 바꿔줘야함 -->
          color : {{item.name}} count : {{item.count}} <button @click="item.count++">수량추가</button>
          <span v-if="item.count === 0">Sold out</span>
          <span v-else>In stock</span> <!-- 조건문을 사용하려면 같은 라인 태그이다. -->
        </li>
      </ul>
      <span>total = {{countTotal}}</span> <!-- 컴피티드..? --> <!-- 데이터의 변경이 되면 그게 조건으로 실행된다. -->

    </el-form>
    <div class="bottomBtns">
      <el-button @click="onSubmit" v-if="!noticeId" type="primary">등록</el-button>
      <el-button type="primary" v-else @click="onModify">수정</el-button>
      <el-button type="primary">취소</el-button>
    </div>
  </section>
</template>
<script>
  import { VueEditor } from 'vue2-editor'
  import axios from 'axios'
  export default {
    components: {
      VueEditor
    },
    computed: {
      countTotal() {
        console.log('==== COMPUTED ====')
        return this.bodyData.reduce((acc, item) => { // this 뷰컴포넌트를 지칭함. acc 누적값, item은 현재값
          return acc + item.count
        }, 0)
      }
    },
    data() {
      return {
        form : {
          subj:'',
          dpTp:'',
          init:[],
          conts:''
        },
        noticeId: this.$route.query.noticeId,
        bodyData : [
          { id:1, count:2, name:'red' },
          { id:2, count:3, name:'green' },
          { id:3, count:5, name:'black' },
          { id:4, count:0, name:'white' },
          { id:5, count:6, name:'pink' }
        ]
      }
    },
    created() {
      if (this.noticeId) {
        console.log('')

        axios.get(`http://13.125.99.42:3000/notice/${this.noticeId}`)
        .then(res => {
          console.log(res)

          this.form = res.data.form

          this.form.dpTp = res.data.dpTp

          console.log('split = ', res.data.form.init.split(','))

          this.form.init = res.data.form.init.split(',')
        })
        .catch(arr => {
          console.log(arr)
        })
        .finally(_ =>{
            
        })
      }
    },
    methods : {
      onSubmit() {
        console.log('=== onSubmit ===')
        console.log(this.form)
        axios({
          method: 'POST',
          url: 'http://13.125.99.42:3000/notice',
          data: { form: this.form }
        })
          .then(res => {
            console.log("성공", res);

            this.$router.push({path:'/noticeDetail', query: {noticeId: res.data.noticeId}})
            //this.$router.push({name:'NoticeDetail', params: {noticeId: res.data.noticeId}})
          })
          .catch(err => {
            console.log("실패", err);
          })
          .finally(_ => {

          })
      },
      onModify() {
        axios.put(`http://13.125.99.42:3000/notice/${this.noticeId}`,{
          form: this.form
        })
          .then(res => {
            console.log(res);
            this.$router.push({path:'/noticeDetail', query: {noticeId: res.data.noticeId}})
          })
          .catch(err => {
            console.log(err);
          })
          .finally(_ => {

          })
      }
    }
  }
</script>
<style>

</style>



